'use client';

import { useState, useEffect, useRef, useCallback } from 'react';
import { DashboardHeader } from '@/components/dashboard/header';
import { MarketData } from '@/components/dashboard/market-data';
import { TradingViewChart } from '@/components/dashboard/tradingview-chart';
import { OrderBook } from '@/components/dashboard/order-book';
import { RecentTrades } from '@/components/dashboard/recent-trades';
import { AiSignal } from '@/components/dashboard/ai-signal';
import { IndicatorSuite } from '@/components/dashboard/indicator-suite';
import { SYMBOLS, TIME_FRAMES } from '@/lib/constants';
import type { IndicatorSettings as IndicatorSettingsType } from '@/lib/indicators';
import { defaultIndicatorSettings } from '@/lib/indicators';
import { VolumePressure } from '@/components/dashboard/volume-pressure';
import { BYBIT_WEBSOCKET_URL, type RecentTrade } from '@/lib/bybit-api';

const MAX_VISIBLE_TRADES = 50;
const MAX_RETRY_ATTEMPTS = 5;
const RETRY_DELAY_BASE = 2000;

export default function Home() {
  const [symbol, setSymbol] = useState(SYMBOLS[0]);
  const [timeframe, setTimeframe] = useState(TIME_FRAMES[3]);
  const [indicatorSettings, setIndicatorSettings] = useState<IndicatorSettingsType>(defaultIndicatorSettings);

  const [trades, setTrades] = useState<RecentTrade[]>([]);
  const [newTradeIds, setNewTradeIds] = useState<Set<string>>(new Set());
  const [wsStatus, setWsStatus] = useState<'connecting' | 'connected' | 'reconnecting' | 'disconnected'>('connecting');
  const wsRef = useRef<WebSocket | null>(null);
  const timeoutIds = useRef<NodeJS.Timeout[]>([]);
  const retryCount = useRef(0);
  
  const connectWebSocket = useCallback(() => {
    wsRef.current?.close();
    setWsStatus(retryCount.current > 0 ? 'reconnecting' : 'connecting');
    if (retryCount.current === 0) {
      setTrades([]);
    }

    const ws = new WebSocket(BYBIT_WEBSOCKET_URL);
    wsRef.current = ws;

    ws.onopen = () => {
      console.log(`Live trades WebSocket connected for ${symbol}`);
      setWsStatus('connected');
      retryCount.current = 0;
      ws.send(JSON.stringify({ op: "subscribe", args: [`publicTrade.${symbol}`] }));
    };

    ws.onmessage = (event) => {
      const data = JSON.parse(event.data);
      if (data.topic && data.topic.startsWith('publicTrade') && Array.isArray(data.data)) {
        const newTrades = data.data.map(t => ({
            execId: t.i,
            price: t.p,
            qty: t.v,
            side: t.S,
            execTime: t.T,
            isBlockTrade: t.m,
        })) as RecentTrade[];
        
        setTrades(prev => [ ...newTrades, ...prev].slice(0, MAX_VISIBLE_TRADES));
        
        const newIds = new Set(newTrades.map(t => t.execId));
        setNewTradeIds(newIds);

        const highlightTimeout = setTimeout(() => {
            setNewTradeIds(current => {
                const updated = new Set(current);
                newIds.forEach(id => updated.delete(id));
                return updated;
            });
        }, 1500);
        timeoutIds.current.push(highlightTimeout);
      }
    };

    ws.onerror = (err) => {
      console.error("Live trades WebSocket error:", err);
    };

    ws.onclose = () => {
      console.log(`Live trades WebSocket disconnected for ${symbol}`);
      if (retryCount.current < MAX_RETRY_ATTEMPTS) {
        const delay = RETRY_DELAY_BASE * Math.pow(2, retryCount.current);
        retryCount.current++;
        console.log(`Attempting to reconnect in ${delay}ms...`);
        const reconnectTimeout = setTimeout(connectWebSocket, delay);
        timeoutIds.current.push(reconnectTimeout);
        setWsStatus('reconnecting');
      } else {
        setWsStatus('disconnected');
      }
    };
  }, [symbol]);

  useEffect(() => {
    retryCount.current = 0;
    connectWebSocket();
    return () => {
      wsRef.current?.close();
      timeoutIds.current.forEach(clearTimeout);
      timeoutIds.current = [];
    };
  }, [connectWebSocket]);


  return (
    <div className="flex flex-col min-h-screen bg-background text-foreground">
      <DashboardHeader
        symbol={symbol}
        setSymbol={setSymbol}
        timeframe={timeframe}
        setTimeframe={setTimeframe}
      />
      <main className="flex-1 p-4 md:p-6 lg:p-8">
        <div className="grid gap-6">
          <MarketData symbol={symbol} />
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            <div className="lg:col-span-2 flex flex-col gap-6">
              <TradingViewChart symbol={symbol} timeframe={timeframe} />
              <AiSignal symbol={symbol} timeframe={timeframe} indicatorSettings={indicatorSettings} />
            </div>
            <div className="flex flex-col gap-6">
              <OrderBook symbol={symbol} />
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-1 gap-6">
                <RecentTrades 
                  trades={trades}
                  newTradeIds={newTradeIds}
                  wsStatus={wsStatus}
                  onReconnect={() => {
                    retryCount.current = 0;
                    connectWebSocket();
                  }}
                />
                <VolumePressure trades={trades} symbol={symbol} />
              </div>
              <IndicatorSuite 
                symbol={symbol} 
                timeframe={timeframe}
                settings={indicatorSettings}
                onSettingsChange={setIndicatorSettings}
              />
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
